.toolbar__notification {
  .uiDropdown__menu {
    padding: 0;
    > li {
      > a,
      > span {
        padding: 4px 15px;
      }
    }
  }

  > ul {
    min-width: 430px;
  }
}

.toolbar__notification__newMessage {
  .icon {
    font-size: $icon-size-small;
    position: relative;
    line-height: 1em;
    top: 2px;
  }
}