// Kill the scroll on the body
.uiModal--open {
  overflow: hidden;
}

// Container that the modal scrolls within
.uiModal {
  display: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $zindex-modal;
  -webkit-overflow-scrolling: touch;
  outline: 0;

  .form-control {
    height: 40px;
  }

  // When fading in the modal, animate it to slide down
  &.fade .uiModal__dialog {
    @include translate(0, -25%);
    @include transition-transform(0.3s ease-out);
  }
  &.in .uiModal__dialog { @include translate(0, 0) }
}

.uiModal--open .uiModal {
  overflow-x: hidden;
  overflow-y: auto;
}

// Shell div to position the modal with bottom padding
.uiModal__dialog {
  position: relative;
  width: auto;
  margin: 10px;
}

// Actual modal
.uiModal__content {
  position: relative;
  background-color: $modal-content-bg;
  border: 1px solid $modal-content-fallback-border-color;
  border: 1px solid $modal-content-border-color;
  border-radius: $border-radius-medium;
  @include box-shadow(0 3px 9px rgba(0,0,0,0.5));
  background-clip: padding-box;
  outline: 0;
}

// Modal background
.uiModal__backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $zindex-modal-background;
  background-color: $modal-backdrop-bg;
  &.fade { @include opacity(0); }
  &.in { @include opacity(0.8); }
}

// Modal header
.uiModal__header {
  padding: $modal-title-padding;
  min-height: ($modal-title-padding + $modal-title-line-height);
  h3 {
    text-transform: uppercase;
    margin-top: 15px;
    font-weight: 200;
  }
}

// Close icon
.uiModal__header .close {
  margin-top: -2px;
}

// Title text within header
.uiModal__title {
  margin: 0;
  line-height: $modal-title-line-height;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.uiModal__body {
  position: relative;
  padding: $modal-inner-padding;
}

// Footer (for actions)
.uiModal__footer {
  @include clearfix;
  border-top: 1px solid $modal-footer-border-color;
  padding: $modal-inner-padding;
  background-color: $color-haiiro-very-lighter;
  @include border-radius(0 0 $border-radius-medium - 1 $border-radius-medium - 1);
  border-top: 1px solid $color-haiiro-lightest;
  box-shadow: 0 1px 0 $color-shiro inset;

  // Properly space out buttons
  .btn + .btn {
    margin-left: 5px;
    margin-bottom: 0;
  }
  // but override that for button groups
  .btn-group .btn + .btn {
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .btn-block + .btn-block {
    margin-left: 0;
  }
}

// Measure scrollbar width for padding body during modal show/hide
.uiModal--scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

// Scale up the modal
@media (min-width: $screen-sm-min) {

  // Automatically set modal's width for larger viewports
  .uiModal__dialog {
    width: $modal-md;
    margin: 30px auto;
  }
  .uiModal__content {
    @include box-shadow(0 3px 9px $dark02);
  }

  // Modal sizes
  .uiModal--sm { width: $modal-sm; }
}

@media (min-width: $screen-md-min) {
  .uiModal--lg { width: $modal-lg; }
}