/*
|--------------------------------------------------------------------------
| Spacing
|--------------------------------------------------------------------------
*/
$toolbar-height: 65px;


/*
|--------------------------------------------------------------------------
| Colors
|--------------------------------------------------------------------------
*/
// Liven
$color-liven: #55d7d7;

// Blue
$color-ao: #5bc0de;
$color-ao-light: lighten($color-ao,15%);
$color-ao-dark: darken($color-ao,15%);

// White
$color-shiro: #ffffff;
$color-shiro-dark: #f8f8f8;
$color-shiro-darker: #f2f2f2;

// Red
$color-aka: #d9534f;
$color-aka-dark: darken($color-aka,15%);

// Orange
$color-orenji: #f0ad4e;
$color-orenji-dark: darken($color-orenji,15%);

// Yellow Orange
$color-kiiro-orenji: #faa71c;
$color-kiiro-orenji-dark: darken($color-kiiro-orenji,6%);

// Green
$color-midori: #5cb85c;
$color-midori-dark: darken($color-midori,10%);

// Gray
$gray: #555;
$gray-light: #777;
$gray-lighter: #999;
$gray-lightest: #bbb;
$gray-very-light: #ddd;
$gray-very-lighter: #eee;
$gray-base: #000;
$gray-dark: #333;
$gray-darker: #222;


$color-haiiro: $gray;
$color-haiiro-light: $gray-light;
$color-haiiro-lighter: $gray-lighter;
$color-haiiro-lightest: $gray-lightest;
$color-haiiro-very-light: $gray-very-light;
$color-haiiro-very-lighter: $gray-very-lighter;
$color-haiiro-dark: $gray-dark;
$color-haiiro-darker: $gray-darker;

// Black
$color-kuro: $gray-base;

// Message Color
$color-msg: #888;

// Brand Colors
$brand-primary: $color-kiiro-orenji;
$brand-liven: $color-liven;
$brand-success: $color-midori;
$brand-info: $color-ao;
$brand-warning: $color-kiiro-orenji;
$brand-danger: $color-aka;

// Link Colors
$link-color: $color-ao;
$link-hover-color: $color-ao-dark;

$link-color-dark: $color-haiiro-light;
$link-hover-color-dark: $color-haiiro;

$link-color-light: $color-haiiro-lighter;
$link-hover-color-light: $color-haiiro-light;

// Text Colors
$text-color: #3a3a3a;

// Toolbar
$toolbar-border: #ddd;

// Body
$body-bg: #f4f4f4;

// Border
$border-color: #dee5e7;


/*
|--------------------------------------------------------------------------
| Close
|--------------------------------------------------------------------------
*/
$close-font-weight: bold;
$close-color: $color-kuro;
$close-text-shadow: 0 1px 0 $color-shiro;

/*
|--------------------------------------------------------------------------
| Transparency
|--------------------------------------------------------------------------
*/
$dark006: rgba(0,0,0,0.06);
$dark008: rgba(0,0,0,0.08);
$dark02: rgba(0,0,0,0.2);
$dark03: rgba(0,0,0,0.3);
$dark04: rgba(0,0,0,0.4);
$dark05: rgba(0,0,0,0.5);
$light02:rgba(255,255,255,0.2);
$light08:rgba(255,255,255,0.8);
$light09:rgba(255,255,255,0.9);


/*
|--------------------------------------------------------------------------
| Border Radius
|--------------------------------------------------------------------------
*/
$border-radius-xsmall: 2px;
$border-radius-small: 3px;
$border-radius-medium: 6px;
$border-radius-large: 8px;
$border-radius-xlarge: 10px;
$border-radius-rounded: 50%;

// Base Border Radius
$border-radius-base: $border-radius-xsmall;

// Input Border Radius
$input-border-radius: $border-radius-base;
$input-border-radius-large: $border-radius-large;
$input-border-radius-small: $border-radius-small;

// Button Border Radius
$btn-border-radius-base: $border-radius-base ;
$btn-border-radius-large: $border-radius-large ;
$btn-border-radius-small: $border-radius-small ;


/*
|--------------------------------------------------------------------------
| Links
|--------------------------------------------------------------------------
*/
$link-hover-decoration: none;


/*
|--------------------------------------------------------------------------
| Typography
|--------------------------------------------------------------------------
*/
$font-family-base: 'Open Sans', sans-serif;
$icon-family-base: 'pogoal-icon';

$font-size-xsmall: 11px;
$font-size-small: 14px;
$font-size-medium: 15px;
$font-size-large: 26px;
$font-size-xlarge: 28px;
$font-size-xxlarge: 30px;
$font-size-xxxlarge: 36px;

// Base Font Size
$font-size-base: $font-size-small;

// Toolbar Font Size
$icon-size-small: 18px;
$icon-size-medium: 22px;
$icon-size-large: 28px;


/*
|--------------------------------------------------------------------------
| Line Height
|--------------------------------------------------------------------------
*/
$line-height-one: 1em;


/*
|--------------------------------------------------------------------------
| Component
|--------------------------------------------------------------------------
|
| Define common padding and border radius sizes and more.
|
*/

$component-active-color: $color-shiro;
$component-active-bg: $brand-primary;

$padding-medium-vertical: 8px;
$padding-medium-horizontal: 14px;


/*
|--------------------------------------------------------------------------
| Image
|--------------------------------------------------------------------------
*/
$img-size-xsmall: 25px;
$img-size-small: 30px;
$img-size-medium: 40px;
$img-size-xlarge: 96px;

// Avatar Image Size
$avatar-size-xsmall: $img-size-xsmall;
$avatar-size-small: $img-size-small;
$avatar-size-medium: $img-size-medium;
$avatar-size-xlarge: $img-size-xlarge;


/*
|--------------------------------------------------------------------------
| Button
|--------------------------------------------------------------------------
|
| For each of Bootstrap's buttons, define text, background and border color.
|
*/
$btn-default-bg: $color-shiro;

$btn-primary-color: $color-shiro;
$btn-primary-bg: $brand-primary;
$btn-primary-border: darken($btn-primary-bg, 5%);

$btn-success-color: $color-shiro;
$btn-success-bg: $brand-success;
$btn-success-border: darken($btn-success-bg, 5%);

$btn-info-color: $color-shiro;
$btn-info-bg: $brand-info;
$btn-info-border: darken($btn-info-bg, 5%);

$btn-warning-color: $color-shiro;
$btn-warning-bg: $brand-warning;
$btn-warning-border: darken($btn-warning-bg, 5%);

$btn-danger-color: $color-shiro;
$btn-danger-bg: $brand-danger;
$btn-danger-border: darken($btn-danger-bg, 5%);

$btn-link-disabled-color: $color-haiiro-very-lighter;


/*
|--------------------------------------------------------------------------
| Z-index
|--------------------------------------------------------------------------
*/
$zindex-navbar: 1000;
$zindex-dropdown: 2000;
$zindex-popover: 1060;
$zindex-tooltip: 2010;
$zindex-toolbar-fixed: 1030;
$zindex-modal-background: 1040;
$zindex-modal: 1080;


/*
|--------------------------------------------------------------------------
| Form
|--------------------------------------------------------------------------
*/
$input-border: #d9d9d9;
$input-color: $color-haiiro;
$input-height-base: $font-size-xxxlarge;
$input-border-focus: $color-ao;
$input-color-placeholder: #ccc;

// Form states and alerts
$state-success-text: $brand-success;
$state-success-bg: #dff0d8;
$state-success-border: darken(adjust-hue($state-success-bg, -10), 5%);

$state-info-text: $brand-info;
$state-info-bg: #d9edf7;
$state-info-border: darken(adjust-hue($state-info-bg, -10), 7%);

$state-warning-text: $brand-warning;
$state-warning-bg: #fcf8e3;
$state-warning-border: darken(adjust-hue($state-warning-bg, -10), 5%);

$state-danger-text: $brand-danger;
$state-danger-bg: #f2dede;
$state-danger-border: darken(adjust-hue($state-danger-bg, -10), 5%);


/*
|--------------------------------------------------------------------------
| Dropdowns
|--------------------------------------------------------------------------
|
| Dropdown menu container and contents.
|
*/
// Background for the dropdown menu.
$dropdown-bg: $color-shiro;

// Dropdown link text color.
$dropdown-link-color: $color-haiiro-light;

// Hover color for dropdown links.
$dropdown-link-hover-color: $color-haiiro-light;

// Disabled dropdown menu item background color.
$dropdown-link-disabled-color: $color-haiiro;

// Text color for headers within dropdown menus.
$dropdown-header-color: $color-haiiro;

// Deprecated `$dropdown-caret-color` as of v3.1.0
$dropdown-caret-color: $color-kuro;

// Dropdown caption color
$dropdown-caption-color: $color-shiro;

/*
|--------------------------------------------------------------------------
| Tooltips
|--------------------------------------------------------------------------
*/
// Tooltip text color
$tooltip-color: $color-shiro;

// Tooltip background color
$tooltip-bg: $color-haiiro-dark;
$tooltip-arrow-color: $tooltip-bg;


/*
|--------------------------------------------------------------------------
| Tooltip
|--------------------------------------------------------------------------
*/
// Thumbnail background color
$thumbnail-bg: $color-shiro;


/*
|--------------------------------------------------------------------------
| Width Size
|--------------------------------------------------------------------------
*/
$width-size-sm: 400px;
$width-size-md: 600px;
$width-size-lg: 900px;


/*
|--------------------------------------------------------------------------
| Modal
|--------------------------------------------------------------------------
*/
// Background color of modal content area
$modal-content-bg: $color-shiro;

// Modal content border color
$modal-content-border-color: $dark02;

// Modal backdrop background color
$modal-backdrop-bg: $color-kuro;

// Modal size
$modal-sm: $width-size-sm;
$modal-md: $width-size-md;
$modal-lg: $width-size-lg;


/*
|--------------------------------------------------------------------------
| uiBox
|--------------------------------------------------------------------------
*/
// Size
$uiBox-lg: $width-size-lg;
$uiBox-md: $width-size-md;
$uiBox-sm: $width-size-sm;


/*
|--------------------------------------------------------------------------
| uiGrid
|--------------------------------------------------------------------------
*/
// Scaffolding
$uiGrid-min-height: 100px;

// Border
$uiGrid-border-color: $border-color;
$uiGrid-border-radius: $border-radius-xsmall;

// Title
$uiGrid-title-color: $color-haiiro-light;

// Meta
$uiGrid-meta-color: $color-haiiro-lighter;

// Footer
$uiGrid-footer-height: 40px;
$uiGrid-footer-bg-color: #f3f3f3;
$uiGrid-footer-hover-bg-color: $color-haiiro-very-lighter;
$uiGrid-footer-color: $color-haiiro-lighter;

// Organizer
$uiGrid-organizer-color: $color-haiiro-lighter;

// Bookmark
$uiGrid-bookmark-size: 18px;

// Member Count
$uiGrid-join-size: $uiGrid-bookmark-size;

// Cover
$uiGrid-hover-cover: $color-shiro;


/*
|--------------------------------------------------------------------------
| Sidebar
|--------------------------------------------------------------------------
*/
// color
$sidebar-border-color: $gray-very-light;


/*
|--------------------------------------------------------------------------
| uiList
|--------------------------------------------------------------------------
*/
// Title
$uiList-name-color: $color-haiiro-lighter;

// Meta
$uiList-meta-color: #aaa;

// Height
// $uiList-body-height: 40px;
// $uiList-item-height: $uiList-body-height + 20;

// Avatar
$uiList-avatar-size: $avatar-size-small;