.imgCover {
  @include transition(opacity 0.15s linear 0s);
  background-color: $uiGrid-hover-cover;
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  left: 0;
  top: 0;
}

.imgCovers {
  position: relative;
  &:hover {
    .imgCover {
      opacity: 0.2;
    }
  }
}

.imgCover--rounded {
  @include border-radius($border-radius-rounded);
}

.imgCover--topRoundMedium {
  @include border-radius($border-radius-medium $border-radius-medium 0 0)
}
