.notification {

}

.notification__counter {
  @include box-shadow(-1px 1px 0px rgba(0, 0, 0, 0.2));
  border-radius: $border-radius-small;
  line-height: $line-height-one;
  background-color: $color-aka;
  font-size: $font-size-xsmall;
  color: $color-shiro;
  position: absolute;
  padding: 3px 5px;
  right: 0;
  top: 15px;
  z-index: 3;
}

.notification--new {
  border-left: 5px solid $color-midori !important;
}

.notification__header {
  position: relative;
  padding: 12px 15px;
  display: table;
  width: 100%;
  .btn {
    float: right;
  }
  &.arrow {
    &--down {
      &:after {
        bottom: -6px;
        left: 18px;
        border-top-color: $color-ao;
      }
    }
  }
}

.notification__footer {
  @include box-shadow(0px -1px 3px 1px $dark006);
  a {
    @include dropdown-list-style-hover;
    @include dropdown-list-style;
    padding: 4px 15px;
  }
}

.notification__list {
  background-color: $color-shiro-dark;
  > li {
    @include dropdown-list-style-hover;
    @include dropdown-list-style;
    border-bottom: 1px solid $color-haiiro-very-lighter;
    border-left: 5px solid $color-haiiro-very-lighter;
    padding: 10px 15px;
  }
}