.uiBox {
  @include border-radius($border-radius-medium);
  @include center-block;
  background-color: $color-shiro;
  margin-bottom: 20px;

  // Size
  &--sm {
    width: $uiBox-sm;
  }
  &--md {
    width: $uiBox-md;
  }
  &--lg {
    width: $uiBox-lg;
  }
}

.uiBox__Header {
  border-bottom: 1px solid $color-haiiro-very-light;
  color: $color-haiiro-light;
  padding: 15px;
  margin-bottom: 15px;
}

.uiBox__Body {
  color: $color-haiiro-lighter;
  padding: 15px;

  h4 {
    margin: 15px 0 20px;
  }
}

.uiBox__Footer {
  border-radius: 0 0 $border-radius-medium $border-radius-medium;
  padding: 15px;
  color: $color-shiro;
}

.uiBox__Footer__btn {
  padding: 0;

  input {
    @include border-radius(0 0 $border-radius-medium $border-radius-medium);
    font-size: $font-size-medium;
    text-transform: uppercase;
    padding: 20px 15px;
  }
}

