.uiDropdown__menu {
  &.toolbar__user {
    @include box-shadow(none);
    @include border-radius(0 0 $border-radius-base $border-radius-base);
    background-color: $color-shiro-dark;
    min-width: 250px;
    border-color: $toolbar-border;
    border-top: 0;
    margin-top: -1px;

    > li {
      > a {
        &:hover {
          background-color: $color-shiro-darker;
        }
      }
    }

    .uiDropdown__menu__header {
      &:after {
        width: 233px;
      }
    }

    .toolbar__userInfo {
      padding: 0 15px;
      margin: 10px 0;
    }

    .avatar--xlg {
      @include border-radius($border-radius-large);
      border: 2px solid #ddd;
      margin-bottom: 10px;
    }

    .toolbar__userName {
      font-size: $font-size-medium + 1;
      padding: 0;
    }

    &:before,
    &:after {
      display: none;
    }
  }
}