/*
|--------------------------------------------------------------------------
| Dropdown Menus
|--------------------------------------------------------------------------
*/

// Dropdown arrow/caret
.caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top:   $caret-width-base dashed;
  border-top:   $caret-width-base solid \9; // IE8
  border-right: $caret-width-base solid transparent;
  border-left:  $caret-width-base solid transparent;
}

// The dropdown wrapper (div)
.uiDropup,
.uiDropdown {
  position: relative;
}

// Prevent the focus on the dropdown toggle when closing dropdowns
.uiDropdown--toggle:focus {
  outline: 0;
}

// The dropdown menu (ul)
.uiDropdown__menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: $zindex-dropdown;
  display: none;
  float: left;
  min-width: 200px;
  padding: 10px 0;
  margin: 3px 0 0;
  list-style: none;
  font-size: $font-size-base;
  text-align: left;
  background-color: $dropdown-bg;

  // IE8 fallback
  border: 1px solid $dropdown-fallback-border;
  border: 1px solid $dropdown-border;
  border-radius: $border-radius-base;
  @include box-shadow(0 3px 8px rgba(0,0,0,0.3));
  background-clip: padding-box;

  // dropdown menu header
  .uiDropdown__menu__header {
    position: relative;
    margin-top: 5px;
    &:after {
      border-top: 1px solid $toolbar-border;
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      width: 185px;
      z-index: 5;
    }
    span {
      color: $color-haiiro-lighter;
      background-color: $color-shiro-dark;
      display: inline;
      font-size: 11px;
      padding: 0 10px 0 15px;
      position: relative;
      text-transform: uppercase;
      z-index: 10;
    }
  }

  // Aligns the dropdown menu to right
  &.pull-right {
    right: 0;
    left: auto;
  }

  // Dividers (basically an hr) within the dropdown
  .divider {
    @include nav-divider($dropdown-divider-bg);
  }

  // Icons within the dropdown menu
  .icon {
    margin-right: 5px;
  }

  // Links within the dropdown menu
  > li {
    > a,
    > span {
      @include dropdown-list-style;
    }
  }

  // Dropdown caption
  .uiDropdown__caption {
    background-color: $color-ao;
    span {
      color: $dropdown-caption-color;
    }
  }

  &:before {
    position: absolute;
    top: -7px;
    left: 9px;
    display: inline-block;
    border-right: 7px solid transparent;
    border-bottom: 7px solid $dropdown-border;
    border-left: 7px solid transparent;
    content: '';
  }

  &:after {
    position: absolute;
    top: -6px;
    left: 10px;
    display: inline-block;
    border-right: 6px solid transparent;
    border-bottom: 6px solid $color-shiro;
    border-left: 6px solid transparent;
    content: '';
  }

  // arrows
  &--arrow-adj-a {
    &:before {
      left: auto;
      right: 17px;
    }
    &:after {
      left: auto;
      right: 18px;
    }
  }

  &--arrow-adj-b {
    &:before {
      left: auto;
      right: 11px;
    }
    &:after {
      left: auto;
      right: 12px;
    }
  }

  &--arrow-adj-c {
    &:before {
      left: auto;
      right: 11px;
    }
    &:after {
      left: auto;
      right: 12px;
    }
  }

  &--arrow-adj-d {
    &:before {
      left: auto;
      right: 11px;
    }
    &:after {
      left: auto;
      right: 12px;
    }
  }
}

// Hover/Focus state
.uiDropdown__menu > li > a {
  @include dropdown-list-style-hover;
}

// Active state
.uiDropdown__menu > .active > a {
  &,
  &:hover,
  &:focus {
    color: $dropdown-link-active-color;
    text-decoration: none;
    outline: 0;
    background-color: $dropdown-link-active-bg;
  }
}

// Disabled state
.uiDropdown__menu > .disabled > a {
  &,
  &:hover,
  &:focus {
    color: $dropdown-link-disabled-color;
  }

  // Nuke hover/focus effects
  &:hover,
  &:focus {
    text-decoration: none;
    background-color: transparent;
    // Remove CSS gradient
    background-image: none;
    @include reset-filter;
    cursor: $cursor-disabled;
  }
}

// Open state for the dropdown
.open {
  // Show the menu
  > .uiDropdown__menu {
    display: block;
  }

  // Remove the outline when :focus is triggered
  > a {
    outline: 0;
  }
}


/*
|--------------------------------------------------------------------------
| Menu positioning
|--------------------------------------------------------------------------
|
| Add extra class to `.dropdown-menu` to flip the alignment of the dropdown
| menu with the parent.
|
*/
.uiDropdown__menu--right {
  left: auto; // Reset the default from `.dropdown-menu`
  right: 0;
}


/*
|
| With v3, we enabled auto-flipping if you have a dropdown within a right
| aligned nav component. To enable the undoing of that, we provide an override
| to restore the default dropdown menu alignment.
|
| This is only for left-aligning a dropdown menu within a `.navbar-right` or
| `.pull-right` nav component.
|
*/
.uiDropdown__menu--left {
  left: 0;
  right: auto;
}

// Dropdown section headers
.uiDropdown__header {
  display: block;
  padding: 3px 20px;
  font-size: $font-size-xsmall;
  line-height: $line-height-base;
  color: $dropdown-header-color;
  white-space: nowrap; // as with > li > a
}

// Backdrop to catch body clicks on mobile, etc.
.uiDropdown__backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: ($zindex-dropdown - 10);
}

// Right aligned dropdowns
.pull-right > .uiDropdown__menu {
  right: 0;
  left: auto;
}


/*
|
| Allow for dropdowns to go bottom up (aka, dropup-menu)
|
| Just add .dropup after the standard .dropdown class and you're set, bro.
| TODO: abstract this so that the navbar fixed styles are not placed here?
|
*/
.uiDropup,
.navbar-fixed-bottom .uiDropdown {
  // Reverse the caret
  .caret {
    border-top: 0;
    border-bottom: $caret-width-base dashed;
    border-bottom: $caret-width-base solid \9; // IE8
    content: "";
  }
  // Different positioning for bottom up menu
  .uiDropdown__menu {
    top: auto;
    bottom: 100%;
    margin-bottom: 2px;
  }
}


/*
|--------------------------------------------------------------------------
| Component alignment
|--------------------------------------------------------------------------
|
| Reiterate per navbar.less and the modified component alignment there.
|
*/
@media (min-width: $grid-float-breakpoint) {
  .navbar-right {
    .uiDropdown__menu {
      right: 0;
      left: auto;
    }


    /*
    |
    | Necessary for overrides of the default right aligned menu.
    | Will remove come v4 in all likelihood.
    |
    */
    .uiDropdown__menu--left {
      left: 0;
      right: auto;
    }
  }
}