// Toolbar position
.toolbar--fixedTop {
  @extend .position-fixed;
  top: 0;
}

// change toolbar position while modal is opened
.uiModal--open {
  .toolbar--fixedTop {
    position: absolute;
  }
}

#toolbar {
  height: $toolbar-height;
  position: relative;
  width: 100%;
  z-index: 100;

  .toolbar__container {
    // @include gradient-vertical($color-orenji);
    @include box-shadow(-1px 1px 2px rgba(0, 0, 0, 0.1));
    // border-bottom: 1px solid $toolbar-border;
    background-color: $color-shiro;
    height: $toolbar-height;
    padding-right: 15px;
    padding-left: 15px;
  }
}

// Main
.toolbar__main {
  float: left;

  h1 {
    font-size: $line-height-base;
    margin: 6px 15px 0 0;
    float: left;

    #brand {
      font-size: $font-size-xxxlarge;
      color: $color-kiiro-orenji;
    }
  }
}

// Minor
.toolbar__minor {
  > ul {
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
      float: right;
      max-height: $toolbar-height;
    }
  }
}

.toolbar__icons {
  > .icon {
    padding: 20px 10px 19px;
    cursor: pointer;
    font-size: $icon-size-small;
  }

  > button{
    color: $color-haiiro-lighter;
    &:hover {
      color: $brand-primary;
    }
  }
}

.toolbar__userAvatar {
  margin-right: 10px;

  &.open {
    background-color: $color-shiro-dark;
  }

  button {
    padding: 18px 10px 17px;
    cursor: pointer;
  }

  &:hover {
    .avatar--sm {
      @include box-shadow(0 0 3px 1px $dark02);
    }
  }

  .uiDropdown__caption {
    border-radius: $border-radius-xsmall $border-radius-xsmall 0 0;
  }

  .uiDropdown__menu {
    &:after {
      border-bottom: 6px solid $color-haiiro-lightest;
    }
  }
}

.toolbar__createEvent__btn {
  text-transform: uppercase;
  padding: 8px 12px;
  margin-top: 14px;
}