@font-face {
  font-family: 'pogoal-icon';
  src:url('/assets/fonts/pogoal-icon.eot?4shq57');
  src:url('/assets/fonts/pogoal-icon.eot?4shq57#iefix') format('embedded-opentype'),
  url('/assets/fonts/pogoal-icon.ttf?4shq57') format('truetype'),
  url('/assets/fonts/pogoal-icon.woff?4shq57') format('woff'),
  url('/assets/fonts/pogoal-icon.svg?4shq57#pogoal-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon {
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  font-family: 'pogoal-icon';
  line-height: $line-height-base;
  font-size: inherit;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  // Size
  &--md {
    font-size: $icon-size-medium;
  }
  &--lg {
    font-size: $icon-size-medium;
  }

  // space
  &--mR {
    margin-right: 5px;
  }
}

