.page__sidebar {
  min-height: 100px;
  padding: 0;
}

.sidebar__item {
  margin-bottom: 25px;

  > ul {
    margin-bottom: 0;

    > li {
      @include box-shadow(0 1px 0 $color-shiro inset);
      border-bottom: 1px solid $sidebar-border-color;
      > a {
        display: block;
        padding: 10px 0;
      }
    }
  }
}

.sidebar__title {
  border-bottom: 1px solid $sidebar-border-color;
  color: darken($color-ao-dark,10%);
  text-transform: uppercase;
  font-weight: bold;
  padding-bottom: 15px;

  .icon {
    color: darken($color-ao-dark,10%);
    font-size: $font-size-medium;
  }
}


/*
|--------------------------------------------------------------------------
| Phone Size
|--------------------------------------------------------------------------
*/
@media (min-width: $screen-sm-min)
{
  .page__sidebar {
    @include box-shadow(1px 0 0 $color-shiro inset);
    border-left: 1px solid $sidebar-border-color;
    padding-left: 30px;
  }
}