#slider {
  color: $color-shiro;
  position: relative;
  height: 300px;
  .hero-slider {
    position: relative;
    .hero-slider-imgs {
      width: 100%;
      z-index: 2;
      .hero-slider-img {
        display: none;
        height: 300px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
    .hero-slider-contents {
      display: table;
      vertical-align: middle;
      width: 100%;
      height: 100%;
      background-color: $dark02;
      position: absolute;
      top: 0;
      left: 0;
      .hero-slider-content {
        @include center-margin;
        height: 300px;
        padding-left: 15px;
        padding-right: 15px;
        display: table-cell;
        text-align: center;
        vertical-align: middle;
        h1 {
          font-weight: 400;
          margin-top: 30px;
        }
        .hero-slider-content-p {
          font-size: 15px;
          letter-spacing: 0.5px;
          margin-bottom: 20px;
        }
        button {
          text-transform: uppercase;
        }
      }
    }
  }
}