* {
  @include box-sizing(border-box);
}
*:before,
*:after {
  @include box-sizing(border-box);
}


/*
|--------------------------------------------------------------------------
| Body Reset
|--------------------------------------------------------------------------
*/
html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
}

#content {
  padding-top: 40px;
  padding-bottom: 40px;
}

/*
|--------------------------------------------------------------------------
| Reset fonts for relevant elements
|--------------------------------------------------------------------------
*/
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}


/*
|--------------------------------------------------------------------------
| Links
|--------------------------------------------------------------------------
*/
a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }

  &:focus {
    @include tab-focus;
  }
}

.link--dark {
  color: $link-color-dark;

  &:hover,
  &:focus {
    color: $link-hover-color-dark;
  }
}

.link--bold--light {
  color: $link-color-light;
  font-weight: bold;

  &:hover,
  &:focus {
    color: $link-hover-color-light;
  }
}


/*
|--------------------------------------------------------------------------
| Figures
|--------------------------------------------------------------------------
*/
figure {
  margin: 0;
}


/*
|--------------------------------------------------------------------------
| Images
|--------------------------------------------------------------------------
*/
img {
  vertical-align: middle;
}
.img-responsive {
  @include img-responsive;
}
.img-rounded {
  border-radius: $border-radius-large;
}
.img-thumbnail {
  padding: $thumbnail-padding;
  line-height: $line-height-base;
  background-color: $thumbnail-bg;
  border: 1px solid $thumbnail-border;
  border-radius: $thumbnail-border-radius;
  @include transition(all .2s ease-in-out);
  @include img-responsive(inline-block);
}
.img-circle {
  border-radius: 50%;
}


/*
|--------------------------------------------------------------------------
| Horizontal rules
|--------------------------------------------------------------------------
*/
hr {
  margin-top: 15px;
  margin-bottom: 15px;
  border: 0;
  border-top: 1px solid $color-haiiro-very-light;
}


/*
|--------------------------------------------------------------------------
| iOS "clickable elements" fix for role="button"
|--------------------------------------------------------------------------
*/
[role="button"] {
  cursor: pointer;
}


/*
|--------------------------------------------------------------------------
| Arrow
|--------------------------------------------------------------------------
*/
@import "arrow";


/*
|--------------------------------------------------------------------------
| Clearing
|--------------------------------------------------------------------------
*/
.clearfix {
  @include clearfix;
}


/*
|--------------------------------------------------------------------------
| List
|--------------------------------------------------------------------------
*/
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/*
|--------------------------------------------------------------------------
| Link
|--------------------------------------------------------------------------
*/
a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: none;
  }
}



/*
|--------------------------------------------------------------------------
| Container
|--------------------------------------------------------------------------
*/
.container-fluid {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}


/*
|--------------------------------------------------------------------------
| Position
|--------------------------------------------------------------------------
*/
.position-fixed {
  position: fixed;
  right: 0;
  left: 0;
}


/*
|--------------------------------------------------------------------------
| Screen Reader
|--------------------------------------------------------------------------
*/
.hidden-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}