.toolbar__main {
  .uiDropdown__menu {
    margin-top: 17px;
  }
}

.toolbar__search {
  position: relative;
  width: 300px;
  margin-top: 14px;
  float: left;

  .input-group-btn {
    .btn {
      border: 1px solid $input-border;
      line-height: $line-height-one;
      color: $color-haiiro-lighter;
      padding: 7px 12px;
      border-left: 0;
    }
  }

  .form-control {
    &:focus {
      ~ .input-group-btn {
        .btn {
          border-color: $input-border-focus;
          color: $color-ao;
        }
      }
    }
  }
}

.toolbar__searchResult {
  width: 100%;

  &:before {
    left: 50%;
    margin-left: -7px;
  }

  &:after {
    left: 50%;
    margin-left: -6px;
  }
}