.uiGrids--vertical {

  .uiGrid__img {
    img {
      @include border-radius($uiGrid-border-radius - 1 $uiGrid-border-radius - 1 0 0);
    }
  }

  .uiGrid__date {
    text-transform: uppercase;
  }
}


/*
|--------------------------------------------------------------------------
| Phone Size
|--------------------------------------------------------------------------
*/
@media (max-width: $screen-sm-min) and (min-width: $screen-xs-min - 1)
{
  .uiGrids--vertical {
    .uiGrid__body {
      position: relative;
      margin-right: auto;
      margin-left: auto;
      display: table;
    }

    .uiGrid__img {
      float: left;
      width: 45%;

      img {
        @include border-radius($uiGrid-border-radius - 1 0 0 $uiGrid-border-radius - 1);
      }
    }

    .uiGrid__content {
      position: absolute;
      float: left;
      width: 55%;
      right: 0;
      top: 0;
    }

    .uiGrid__footer {
      @include border-radius(0 0 $uiGrid-border-radius - 1);
      position: absolute;
      float: left;
      width: 55%;
      bottom: 0;
      right: 0;
    }

    .uiGrid__organizer {
      a {
        @include border-radius(0);
      }
    }
  }
}