button,
[type="submit"] {
  -ms-touch-action: manipulation;
  background-color: transparent;
  touch-action: manipulation;
  -webkit-user-select: none;
  background-image: none;
  -moz-user-select: none;
  -ms-user-select: none;
  white-space: nowrap;
  user-select: none;
  cursor: pointer;
  border: none;
  padding: 0;
  margin: 0;
}

.btn {
  display: inline-block;
  margin-bottom: 0;
  font-weight: $btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  white-space: nowrap;
  @include button-size($padding-base-vertical, $padding-base-horizontal, $font-size-base, $line-height-base, $btn-border-radius-base);
  @include user-select(none);

  &.small {
    @extend .small;
    line-height: $line-height-one;
  }

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      @include tab-focus;
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: $btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    @include box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: $cursor-disabled;
    @include opacity(.65);
    box-shadow: none;
  }
}

a.btn {
  &.disabled,
  fieldset[disabled] & {
    pointer-events: none;
  }
}


/*
|--------------------------------------------------------------------------
| Colors
|--------------------------------------------------------------------------
*/
.btn-default {
  @include button-variant($btn-default-color, $btn-default-bg, $btn-default-border);
}
.btn-primary {
  @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);
}

// Success appears as green
.btn-success {
  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border);
}

// Info appears as blue-green
.btn-info {
  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border);
}

// Warning appears as orange
.btn-warning {
  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border);
}

// Danger and error appear as red
.btn-danger {
  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border);
}

// Green
.btn-midori {
  color: #fff;
  background-color: $color-midori;
}

// Yellow Orange
.btn-kiiro-orenji {
  color: $color-shiro;
  background-color: $color-kiiro-orenji;

  &:focus,
  &:hover {
    background-color: $color-kiiro-orenji-dark;
    color: $color-shiro;
  }
}

// Blue
.btn-ao {
  color: $color-shiro;
  background-color: $color-ao;

  &:focus,
  &:hover {
    background-color: $color-ao-dark;
    color: $color-shiro;
  }
}

// Dark Blue
.btn-ao-dark {
  color: $color-shiro;
  background-color: $color-ao-dark;

  &:focus,
  &:hover {
    background-color: darken($color-ao-dark, 5%);
    color: $color-shiro;
  }
}


/*
|--------------------------------------------------------------------------
| Link buttons
|--------------------------------------------------------------------------
*/
// Make a button look and behave like a link
.btn-link {
  color: $link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    box-shadow: none;
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: $btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


/*
|--------------------------------------------------------------------------
| Button size
|--------------------------------------------------------------------------
*/
.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  @include button-size($padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $btn-border-radius-small);
}
.btn-md {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-medium-vertical, $padding-medium-horizontal, $font-size-medium, $line-height-small, $btn-border-radius-small);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  @include button-size($padding-small-vertical, $padding-small-horizontal, $font-size-small, $line-height-small, $btn-border-radius-small);
}
.btn-xs {
  @include button-size($padding-xs-vertical, $padding-xs-horizontal, $font-size-small, $line-height-small, $btn-border-radius-small);
}


/*
|--------------------------------------------------------------------------
| Block button
|--------------------------------------------------------------------------
*/
.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}